import {Component} from '@angular/core';
import {PagedListingComponentBase, PaginationParameters} from "@shared/util/paged-listing-component-base";
import {OperationService, PrOperation} from "@shared/service-proxies/service/operation.service";
import {finalize} from "rxjs/operators";
import {HttpResponse} from "@angular/common/http";


@Component({
  selector: 'app-plan-list',
  templateUrl: './plan-list.component.html'
})
export class PlanListComponent extends PagedListingComponentBase {

  loading = false;
  dataItems: PrOperation[] = [];
  pagination = new PaginationParameters({ sidx: 'created_date', sord:'desc', pageSize: this.pageSize, pageNum: this.pageNum });

  constructor( private operationService: OperationService ){ super(); }


  protected list(finishedCallback: Function): void {
    this.loading = true;

    this.pagination.pageNum = this.pageNum;
    this.pagination.pageSize = this.pageSize;
    this.operationService.getOperationList(this.pagination)
      .pipe(
        finalize(() => {
          this.loading = false;
        })
      )
      .subscribe( resp => {
        if(resp instanceof HttpResponse) {
          let resp200 = resp as HttpResponse<any>;
          if (resp200.status === 200) {
            this.totalItems = resp200.body["total"];
            this.totalPages = resp200.body["pages"];
            this.dataItems = resp200.body["list"];
            this.showPaging(this.totalPages, this.totalItems);
          }
        }
      });

  }


}
