import { OnInit } from '@angular/core';

export class PaginationParameters {
  //json
  conditionJson?:string;
  //page index
  pageNum?: number;
  //page size
  pageSize?: number;
  // fields?: string;
  //created_date
  sidx?: string;
  //desc
  sord?: string;

  constructor(init?: Partial<PaginationParameters>) {
      Object.assign(this, init);
  }
}

export class PageMeta {
  totalItemsCount: number;
  pageSize: number;
  pageNum: number;
  pageCount: number;
  // previousPageLink: string;
  // nextPageLink: string;

  init(data?: any) {
    if (data) {
        this.totalItemsCount = data["totalItemsCount"];
        this.pageSize = data["pageSize"];
        this.pageNum = data["pageNum"];
        this.pageCount = data["pageCount"];
    }
  }
}

export abstract class PagedListingComponentBase implements OnInit {

    public pageSize: number = 5;
    public pageNum: number = 1;
    public totalPages: number = 1;
    public totalItems: number;
    public isTableLoading = false;

    constructor() {

    }

    ngOnInit(): void {
        this.refresh();
    }

    refresh(): void {
        this.getDataPage();
    }

    public showPaging(pTotalPage:number, pTotalItems: number): void {
        this.totalPages = pTotalPage;
        this.totalItems = pTotalItems;
    }

    public getDataPage(): void {
        this.isTableLoading = true;
        this.list(() => {
            this.isTableLoading = false;
        });
    }

    protected abstract list(finishedCallback: Function): void;
}
