import { Injectable } from '@angular/core';
import {BaseService} from "@shared/service-proxies/service/base.service";
import {catchError} from "rxjs/operators";
import {HttpClient} from "@angular/common/http";
import {PaginationParameters} from "@shared/util/paged-listing-component-base";

@Injectable({
  providedIn: 'root'
})
export class OperationService extends BaseService{

  constructor(private httpClient:HttpClient) { super(); }

  getOperationList(paginationParameters: PaginationParameters)  {

    let url_ = this.appUrlBase + "/operation";
    return this.httpClient.get(url_, { observe: 'response',
      params: {pageNum: paginationParameters.pageNum.toString(), pageSize: paginationParameters.pageSize.toString()}}).pipe(
      catchError(this.handleError('getOperationList', []))
    );

  }
}


export interface  PrOperation {
  id: string;
  name: string;

  organizationId: string;
  organizationName: string;
  operationtypeId: string;
  operationTypeName: string;
  statustypeId: string;
  statusTypeLabel: string;

  facts: string;
  begintime: Date ;
  endtime: Date;
  createdby: string;
  creationtime: Date;
  plannedbegintime: Date;
  plannedendtime: Date;
  description: string;
  istemplate: number;
  updatedby: string;
}
