import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {BaseService} from "./base.service";
import {catchError} from "rxjs/operators";
import {Observable} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class OperationTypeService extends BaseService{

  httpHeaders:HttpHeaders = new HttpHeaders({
    "Content-Type": "application/json",
    "Accept": "application/json"
  });

  constructor(private httpClient:HttpClient) {
    super();
  }

  getOperationTypeList() : Observable<PrOperationType[]> {

    let url_ = this.appUrlBase + "/operationtype";
    return this.httpClient.get<PrOperationType[]>(url_).pipe(
      catchError(this.handleError<PrOperationType[]>('getOperationTypeList', []))
    );

  }

}


export interface PrOperationType {
  id: string;
  organizationId: string;
  parentId: string;
  typ: string;
  displayorder: number;
}


