import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'statusColorPipe'
})
export class StatusColorPipePipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    if('' == value)
    {
      return 'green';
    }
    else if('' == value)
    {
      return 'orange';
    }
    else
      return 'yellow';
  }

}
