import { Injectable } from '@angular/core';
import {BaseService} from "@shared/service-proxies/service/base.service";
import {catchError} from "rxjs/operators";
import {HttpClient} from "@angular/common/http";
import {PaginationParameters} from "@shared/util/paged-listing-component-base";
import {Observable} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class OperationUnitService extends BaseService{

  constructor(private httpClient:HttpClient) { super(); }

  getOperationUnitList(unitTypeName: string): Observable<PrOperationUnit[]>  {

    let url_ = this.appUrlBase + "/operationunit/" + unitTypeName + "/?operationId=11";
    return this.httpClient.get<PrOperationUnit[]>(url_).pipe(
      catchError(this.handleError<PrOperationUnit[]>('getOperationList', []))
    );

  }
}


export interface  PrOperationUnit {
  id: string;
  name: string;

  organizationId: string;
  organizationName: string;
  operationtypeId: string;

  unittypeId: string;
  unitsubtypeId: string;
  statustypeId: string;
  createdby: string;
  creationtime: Date;

}
