import {Component, Input, OnInit} from '@angular/core';
import {OperationUnitService, PrOperationUnit} from "@shared/service-proxies/service/operation-unit.service";
import {finalize} from "rxjs/operators";

@Component({
  selector: 'app-operation-unit-list',
  templateUrl: './operation-unit-list.component.html',
  styleUrls: ['./operation-unit-list.component.css']
})
export class OperationUnitListComponent implements OnInit {

  @Input() unitTypeName: string;

  loading = false;
  dataItems: PrOperationUnit[] = [];

  constructor( private operationUnitService: OperationUnitService) { }

  ngOnInit() {
    this.list();
  }

  private list(): void {
    this.loading = true;
    this.operationUnitService.getOperationUnitList(this.unitTypeName)
      .pipe(
        finalize(() => {
          this.loading = false;
        })
      )
      .subscribe(resp => {
        this.dataItems = resp;
      });
  }

}
