import { Component, OnInit } from '@angular/core';
import {NzFormatEmitEvent, NzTreeNode, NzTreeNodeOptions} from 'ng-zorro-antd';
import {OperationTypeService, PrOperationType} from "@shared/service-proxies/service/operation-type.service";



@Component({
  selector: 'app-plansystem',
  templateUrl: './plansystem.component.html',
  styleUrls: ['./plansystem.component.css']
})
export class PlansystemComponent implements OnInit {
  searchValue = '';

  nodes = [];

  nzEvent(event: NzFormatEmitEvent): void {
    console.log(event);
  }

  operationTypes: PrOperationType[] = [];

  constructor(private operationtypeService: OperationTypeService) {

  }

  ngOnInit(): void {
    this.getOperationTypeList();
  }

  getOperationTypeList(): void {

    this.operationtypeService.getOperationTypeList()
      .subscribe(types=> {
        this.operationTypes = types;
        let tempNodes = [];
        if(types != null) {
          for(let ot of this.operationTypes) {
            tempNodes.push({ title: ot.typ, key: ot.id, parent: ot.parentId, isLeaf: true  });
          }

          this.nodes = Object.values(tempNodes.reduce(this.totree, {}));

          // console.log(JSON.stringify(this.nodes));
        }
      });

  }

  totree(branches, node): [] {
    // if we don't have the parent yet
    if(node.parent == null || node.parent == "") {
      // store our node in the full list
      // copy all added branches on possible placeholder
      branches[node.key] = Object.assign(node, branches[node.key]);
      branches[node.key]["parent"] = "";
    }
    else {
      if (branches[node.parent]) {
        if(!branches[node.parent]["children"]) {
          // create a dummy placeholder for now
          branches[node.parent]["children"] = [];
        }
        // store our node in its parent
        branches[node.parent]["children"].push(node);
        branches[node.parent]["isLeaf"] = false;
      }
    }

    return branches;
  }



}
